.header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 15px;
}

.app{
    margin: 15px;
    font-family: 'Indie Flower', cursive;
}

.component-container{
    display: flex;
    overflow: hidden;
}
.list {
    width: 90%;
    height: 511px;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 0;
}

.item {
    list-style-type: none;
    border: 1px solid #000;
    border-bottom: none;
    line-height: 55px;
    height: 50px;
    padding-left: 20px;
}

.item:last-child {
    border: 1px solid #000;
}

.checkbox{
    display: inline-flex;
    line-height: 20px;
}
.button {
    background: none;
    border-left: none;
    font-family: 'Indie Flower', cursive;

}

.button:nth-child(1){
    border-left: 1px solid #dfe9e9;;
}
.input{
    height: 27px;
    width: 200px;
    font-family: 'Indie Flower', cursive;
}
.filler {
    background: #1DA598;
    height: 100%;
    border-radius: inherit;
    transition: width .2s ease-in;
    width: 200px;
}

.progress-bar {
    position: absolute;
    top: 105px;
    left: 30px;
    width: 95%;
    height: 5px;
    border-radius: 50px;
    border: 1px solid #333;
}

.tasks-container {
    display: block;
    width: 70vw;
    margin-left: 20px;
}

.form-tasks-wrapper {
    display: flex;
    justify-content: flex-end;
    padding: 0 100px 0 0;
}

.category-container {
    width: 30vw;
    /*height: 511px;*/
}

.form-category-wrapper {
    display: flex;
}
.btns{
    display: flex;
    justify-content: flex-end;
    margin: 9px;
    position: relative;
    top: -50px;
}
